/* Box sizing rules */
@import url("://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  font-size: 16px;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.preload * {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -ms-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
}

@font-face {
  font-family: "icons";
  src: url("../fonts/fontello.woff2");
  src: url("../fonts/fontello.woff2") format("woff"), url("../fonts/fontello.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
.icons {
  font-style: normal;
  font-family: "icons";
  font-weight: normal;
}

:root {
  --root-font-size: 16px;
  --ff-primary: "Montserrat", serif;
  --fs-100: 0.8125rem;
  --fs-200: 0.875rem;
  --fs-300: 0.9375rem;
  --fs-400: 1rem;
  --fs-500: 1.125rem;
  --fs-600: 1.25rem;
  --fs-700: 1.375rem;
  --fs-800: 1.5rem;
  --fs-900: 1.625rem;
  --fs-1000: 2.25rem;
  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semi-bold: 600;
  --fw-bold: 700;
  --fw-extra-bold: 800;
  --lh-100: 0.75;
  --lh-200: 1;
  --lh-300: 1.25;
  --lh-400: 1.5;
  --lh-500: 2;
  --c-primary: #2994ff;
  --c-black: #17171a;
  --c-white: #fefefe;
  --c-grey-100: #343434;
  --c-grey-200: #0a0a0a;
  --w-large: 1920px;
  --w-medium: 1680px;
  --w-default: 1440px;
  --w-small: 1240px;
  --w-tiny: 800px;
  --t-fast: 0.3s;
  --t-medium: 0.5s;
  --t-slow: 1s;
}
:root .width-lg {
  width: min(var(--w-large), 100%);
}
:root .width-md {
  width: min(var(--w-medium), 100%);
}
:root .width-df {
  width: min(var(--w-default), 100%);
}
:root .width-sm {
  width: min(var(--w-small), 100%);
}
:root .width-xs {
  width: min(var(--w-tiny), 100%);
}

html {
  scroll-behavior: smooth;
}

body {
  position: relative;
  background-color: var(--c-black);
}

.content {
  width: min(var(--w-default), 100%);
  margin-inline: auto;
  padding-inline: 1em;
}

h3, h2, main, body, .text-100 {
  font-family: var(--ff-primary);
  font-size: var(--root-font-size);
  font-weight: var(--fw-regular);
  line-height: var(--lh-400);
  color: var(--c-white);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

.post-item h3, .text-200 {
  font-family: var(--ff-primary);
  font-size: clamp(var(--fs-700), 3vw, var(--fs-900));
  font-weight: var(--fw-regular);
  line-height: var(--lh-400);
  color: var(--c-white);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

.posts h2, .text-300 {
  font-family: var(--ff-primary);
  font-size: clamp(var(--fs-700), 3vw, var(--fs-900));
  font-weight: var(--fw-bold);
  line-height: var(--lh-400);
  color: var(--c-white);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

.header__logo a, h1, .text-400 {
  font-family: var(--ff-primary);
  font-size: var(--fs-1000);
  font-weight: var(--fw-bold);
  line-height: var(--lh-400);
  color: var(--c-white);
  text-decoration: none;
  text-transform: none;
  font-style: normal;
}

main p {
  margin-bottom: 1em;
}
main p:last-of-type {
  margin-bottom: 0;
}

strong {
  font-weight: var(--fw-bold);
}

em {
  font-style: italic;
}

ul, ol {
  margin-left: 1em;
}

a {
  color: var(--c-primary);
  text-decoration: none;
}
a:hover, a:focus {
  color: var(--c-white);
  text-decoration: underline;
}

.button {
  font-size: var(--fs-400);
  font-weight: var(--fw-semi-bold);
  line-height: var(--lh-400);
}

.button {
  text-align: center;
  display: block;
  width: fit-content;
  border-radius: 2em;
  cursor: pointer;
  transition: all var(--t-fast);
}

.button {
  color: var(--c-white);
  background-color: var(--c-primary);
  position: relative;
  padding-block: 1em;
  padding-inline: 2.15em;
  text-decoration: none;
}
.button:hover, .button:focus {
  background-color: var(--c-white);
  color: var(--c-primary);
  text-decoration: none;
}

.icon:before {
  content: "";
  font-family: "icons";
  font-size: var(--fs-500);
  color: var(--c-primary);
}

.icon--address:before {
  content: "\e814";
}

.icon--phone:before {
  content: "\e815";
}

.social {
  list-style: none;
  margin: 0;
}
.social a:before {
  content: "\e800";
  font-family: "icons";
}

.social__instagram a:before {
  content: "\f16d";
}

.social__linkedin a:before {
  content: "\e809";
}

.social__lastfm a:before {
  content: "\f202";
}

.social__github a:before {
  content: "\f09b";
}

.social__codepen a:before {
  content: "\f32c";
}

.header__container {
  display: flex;
  justify-content: space-between;
  padding-block: 3em;
  border-bottom: 1px solid var(--c-grey-100);
}

.header__logo a {
  transition: color var(--t-medium);
}
.header__logo a:hover, .header__logo a:focus {
  color: var(--c-primary);
  text-decoration: none;
}

.header__navigation a, .nav-100 {
  font-family: var(--ff-primary);
  font-size: clamp(var(--fs-400), 3vw, var(--fs-500));
  font-weight: var(--fw-medium);
  line-height: var(--lh-400);
  color: var(--c-white);
  text-decoration: none;
  text-transform: lowercase;
  font-style: normal;
  background-color: transparent;
  border: 1px solid var(--c-grey-100);
  border-radius: 0 1em 0 1em;
  padding: 0.7em 1.25em;
  transition: all var(--t-fast);
}
.header__navigation a:hover, .nav-100:hover, .header__navigation a:focus, .nav-100:focus {
  background-color: var(--c-grey-200);
  color: var(--c-primary);
}

.header__navigation {
  display: flex;
  align-items: center;
}
.header__navigation ul {
  margin: 0;
  list-style: none;
  display: flex;
}
.header__navigation li {
  margin-inline: 0.5em;
}
.footer__container {
  display: flex;
  justify-content: space-between;
  padding-block: 3em;
  border-top: 1px solid var(--c-grey-100);
}

.footer__social ul {
  display: flex;
  gap: clamp(1rem, 2vw, 1.5rem);
}
.footer__social a {
  font-size: clamp(1.5rem, 2vw, 2rem);
  color: var(--c-white);
}

.posts {
  margin-block: 3em;
}
.post-item {
  margin-bottom: 1em;
}
#skip-nav {
  position: absolute;
  top: 0;
  width: 300px;
}
#skip-nav a:link,
#skip-nav a:visited {
  background-color: #fff;
  color: #000;
  font-size: 20px;
  font-weight: 400;
  overflow: hidden;
  padding: 10px;
  position: absolute;
  left: -9999px;
  text-decoration: none;
  width: 1px;
  height: 1px;
}
#skip-nav a:focus {
  left: 0;
  width: auto;
  height: auto;
  outline: 1px solid #000;
  overflow: visible;
  text-decoration: none;
  z-index: 20000;
}
#skip-nav a:hover {
  background-color: #000;
  color: #fff;
}

.site-map {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-map li {
  margin: 0 0 0.5em 0;
}
.site-map li a.not-a-link {
  display: none;
}
.site-map li ul {
  list-style: none;
  margin: 0.5em 0 0 0;
  padding: 0;
}

.br-2 {
  border-radius: 2em;
}

.br-1 {
  border-radius: 1em;
}